import {
	mapState,
	mapMutations
} from 'vuex';
var QQMapWX = require('@/components/qqmap-wx-jssdk.min.js');
var qqmapsdk;
import utils from '@/components/utils.filter.js';
export default {
	data() {
		return {
			avatar: [
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big25002.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big91012.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big25002.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big91012.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big10001.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg',
				'https://ossweb-img.qq.com/images/lol/web201310/skin/big81005.jpg'
			],
			btnText: '立即报名',
			btnEnable: true,
			dataAll: {},
			curScene: null,
			isShopSelf: false,
			menuBorder: false,
			userList: [],
			curUserGroup: [],
			curUserType: 0,
			formId: null
		}
	},
	computed: {
		...mapState(['globalOpenid', 'GlobalUserInfo', 'token', 'globalWXuserInfo', 'globalUserType'])
	},
	onLoad(options) {
		var that = this;
		that.curUserType = that.globalUserType;
		if (options) {
			that.curScene = options.scene;
			that.isShopSelf = options.shopSelf;
		}
		that.dataAll = uni.getStorageSync('taskDetail');
		that.curUserGroup = that.dataAll.userGroup;
		qqmapsdk = new QQMapWX({
			key: 'MJMBZ-5XNKF-YQIJY-JBJ5T-HWOZ5-MSBVR' // 必填
		});
	},


	methods: {
		// 致电商家
		makePhone(phone) {
			uni.makePhoneCall({
				phoneNumber: phone.toString()
			})
		},
		MenuBorder(e) {
			var that = this;
			that.dataAll.userGroup.forEach((item, index) => {
				if (item.id == e.id) {
					item.checked = !item.checked;
					if (item.checked) {
						that.userList.push(item.id)
					}

				}
			})
		},
		navAcceptPeople(data) {
			uni.navigateTo({
				url: '/pages/public/acceptPeople/acceptPeople?data=' + JSON.stringify(data),
			})
		},
		backPage(e) {
			uni.navigateBack({
				delta: 1
			});
		},
		openMyform(e) {
			uni.navigateTo({
				url: '/pages/myUser/myform/myform',
			})
		},
		// 确定人员反馈
		confirUser() {
			var that = this;

			if (that.userList.length > 0) {
				uni.showModal({
					title: '提示',
					content: '确定反馈？',
					success: function(res) {
						if (res.confirm) {
							var tempData = {
								id: that.dataAll.id,
								ids: that.userList
							}
							uni.request({
								url: that.global_baseUrl + '/index.php/nonstudent',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										var text = '已提交该人员反馈，系统将会进行处理！'
										that.navFeedBackPage(text);
									} else if (res.data.code == '701') {
										uni.showToast({
											title: '您已反馈该用户！',
											icon: 'none'
										})
									}
								}
							});
						} else if (res.cancel) {}
					}
				});
			} else {
				uni.showToast({
					title: "未选择用户！",
					icon: 'none'
				})
			}
		},
		// 立即报名
		signUpNow(e) {
			var that = this;
			that.formId = e.detail.formId;
			var formdata = e.detail.value
			if (that.globalUserType == 0) {
				uni.showModal({
					title: '提示',
					content: '确定报名该任务吗？',
					success: function(res) {
						if (res.confirm) {
							that.userSignUpTask();
						} else if (res.cancel) {
							// console.log('用户点击取消');
						}
					}
				});
			} else {
				uni.showToast({
					title: '您是商家，无法报名！',
					icon: 'none'
				})
			}

		},
		// 报名请求接口
		userSignUpTask() {
			var that = this;
			// type0兼职，1服务
			var tempType = 0;
			if (that.curScene == 'service') {
				tempType = 1;
			}
			var tempData = {
				task_id: that.dataAll.id,
				stu_id: that.GlobalUserInfo.id,
				type: tempType
			}
			uni.request({
				url: this.global_baseUrl + '/index.php/jietask',
				data: tempData,
				method: "POST",
				success: (res) => {
					if (res.data.code == 200) {
						// 接单成功
						that.btnText = '已接单';
						that.btnEnable = false;
						var text = '恭喜，成功报名该任务！'
						that.navFeedBackPage(text);
						that.sendModelMessage();
					} else if (res.data.code == '201') {
						uni.showToast({
							title: '接单失败',
							icon: 'none'
						})
					}
				}
			});
		},
		sendModelMessage() {
			var that = this;
			var tempData = {
				openid: that.globalOpenid,
				templateid: '1yHiiXqOfXEJ-n8J7eltPqJqq-regzfigvq5pvjkqD8',
				formid: that.formId,
				orderID: '',
				keyword1: that.dataAll.name,
				keyword2: utils.timeTodate('Y-m-d H:i:s index', that.dataAll.endtime),
				keyword3: that.dataAll.fabu_name,
				keyword4: that.dataAll.phone
			}
			uni.request({
				url: that.global_baseUrl + '/index.php/sendMsg',
				data: tempData,
				method: "POST",
				success: (res) => {
					if (res.data.code == 200) {

					} else if (res.data.code == '201') {

					}
				}
			});
		},
		goMap(e) {
			var that = this;
			uni.openLocation({
				latitude:parseInt(that.dataAll.latitude),
				longitude:parseInt(that.dataAll.longitude),
				name: that.dataAll.address,
				success: function() {
			
				}
			});
			// qqmapsdk.reverseGeocoder({
			// 	location: {
			// 		latitude: that.dataAll.latitude,
			// 		longitude: that.dataAll.longitude,
			// 	},
			// 	success: function(res) { //成功后的回调
			// 		uni.openLocation({
			// 			latitude:parseInt(that.dataAll.latitude),
			// 			longitude:parseInt(that.dataAll.longitude),
			// 			name: that.dataAll.address,
			// 			success: function() {

			// 			}
			// 		});
			// 	}
			// })
		},
		// 任务完成
		taskFinish(e) {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '确认完成该任务？',
				success: function(res) {
					if (res.confirm) {
						var tempData = {
							id: that.dataAll.id
						}
						if (that.curUserType == 0) {
							uni.request({
								url: that.global_baseUrl + '/index.php/staskok',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										var text = '该任务已完成！'
										that.navFeedBackPage(text);
									}
								}
							});
						} else {
							uni.request({
								url: that.global_baseUrl + '/index.php/taskok',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										var text = '该任务已完成！'
										that.navFeedBackPage(text);
									}
								}
							});
						}

					} else if (res.cancel) {}
				}
			});

		},
		// 未到反馈
		noComeFeed(e) {
			this.modalName = e.currentTarget.dataset.target;
		},
		//取消任务
		cancelTask() {
			var that = this;
			uni.showModal({
				title: '提示',
				content: '确认取消该任务',
				success: function(res) {
					if (res.confirm) {
						var tempData = {
							id: that.dataAll.id
						}
						if (that.curUserType == 0) {
							uni.request({
								url: that.global_baseUrl + '/index.php/sdeltask',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										var text = '该任务已取消，首页将不在显示！'
										that.navFeedBackPage(text);
									} else if (res.data.code == '700') {}
								}
							});
						} else {
							uni.request({
								url: that.global_baseUrl + '/index.php/deltask',
								data: tempData,
								method: "POST",
								success: (res) => {
									if (res.data.code == '200') {
										var text = '该任务已取消，首页将不在显示！'
										that.navFeedBackPage(text);
									} else if (res.data.code == '700') {}
								}
							});
						}
					} else if (res.cancel) {}
				}
			});


		},
		navFeedBackPage(text) {
			uni.navigateTo({
				url: '/pages/public/feedbackPage/feedbackPage?content=' + text
			})
		}
	}
}
